
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import md5 from 'vux/src/tools/md5/index.js'

import Scroll from '../../../base/scroll/scroll'
import Info from '../../../base/info/info'
import MButton from '../../../base/button/button'
import { mapActions } from 'vuex'
export default {
name: "registerOne",
components: {
XInput,
Group,
Info,
MButton,
Scroll
},
watch: {
phone(val) {
if (this.$refs.phone.valid) {
this.btnCodeState = true
return false
}
this.btnCodeState = false
this.checkState()
},
msg() {
this.checkState()
},
pwd() {
this.checkState()
}
},
data() {
return {
btnCodeState: false,
btnSaveState: false,
phone: null,
msg: null,
msgType: (val) => {
if (val.length === 6) {
return {
valid: true,
}
} else {
return {
valid: false,
msg: '6'
}
}
},
pwd: null,
pwdType: (val) => {
let str = /((?=.*\d)(?=.*\D)|(?=.*[a-zA-Z])(?=.*[^a-zA-Z]))^.{6,16}$/
if (str.test(val)) {
return {
valid: true,
}
} else {
return {
valid: false,
msg: '6'
}
}
},
qq: null,
codeText: '',
xieyi: '',
infos: [
'QQ'
],
time: 60,
timer: null
}
},
methods: {
checkState() {
let isPhone = this.$refs.phone.valid
let isMsg = this.$refs.msg.valid
let isPwd = this.$refs.pwd.valid
if (isPhone && isMsg && isPwd) {
this.btnSaveState = true
} else {
this.btnSaveState = false
}
},
getCode() {
if (this.btnCodeState) {
this.btnCodeState = false
this.$axios.post('/api/sms/send', {
telephone: this.phone,
type: 0
}).then((response) => {
if (response.data.code === '200') {
this.timer = setInterval(this.timeGo, 1000)
}
}).catch((error) => {
this.btnCodeState = true
this.$vux.alert.show({
title: '',
content: '',
})
})
}
},
timeGo() {
if (this.time >= 0) {
this.codeText = `(${this.time}`
this.time--
} else {
this.codeText = ''
this.time = 60
clearInterval(this.timer)
this.timer = null
this.btnCodeState = true
}
},
doRegister() {
this.$vux.loading.show({
text: ''
})
this.$axios.post('/api/user/register1', {
telephone: this.phone,
password: md5(this.pwd),
qqNum: this.qq
}).then((response) => {
this.$vux.loading.hide()
let data = response.data
if (data.code !== "200") {
this.$vux.alert.show({
title: '',
content: data.message,
})
} else {

this.setUserPhoneHistory(this.phone)

this.$router.push({ name: 'registerTwo' })
}
}).catch((error) => {
this.$vux.loading.hide()
this.$vux.alert.show({
title: '',
content: '',
})
})
},
...mapActions([
'setUserPhoneHistory'
])
}
}



